<template>
  <div id="create-character_skills" class="create-character_skills">
    <h2>Skills</h2>
    <p>
      While your characters ability scores represent their raw talent and potential, skills represent
their training and experience at performing certain tasks. Each skill is keyed to one of your
characters ability scores and used for an array of related actions. Your characters expertise
in a skill comes from several sources, including their background and class. In this chapter,
youll learn about skills, their scope, and the actions they can be used for.
    </p>

    <div class="select-skills">
      <h3>Select Skills to Train</h3>
      <table class="select-skills-table">
      <tr class="select-skills-heading">
        <th>Skill</th><th class="text-center">Total</th><th colspan="2" class="text-center">Ability</th><th class="text-center" colspan="2">Proficiency</th>
      </tr> 
        <tr class="select-skills-row">
          <td class="skill-name">Perception</td>
          <td class="skill-total"></td>
          <td class="ability ability-name">Wis</td>
          <td class="ability-mod">{{displayMod(character.wisdom)}}</td>
          <td class="prof-check-box"></td>
          <td class="prof-bonus">0</td>
        </tr>
        <tr v-for="skill in skillData.skills" :key="'skill-'+skill.name" class="select-skills-row">
          <td class="skill-name">{{skill.name}}</td>
          <td class="skill-total">{{displaySkillBonus(skill)}}</td>
          <td class="ability ability-name">{{skill.ability.substring(0,3)}}</td>
          <td class="ability-mod">{{displayMod(character[skill.ability])}}</td>
          <td class="prof-check-box">
          <input 
            type="checkbox" 
            :value="skill.name" 
            :id="'skill-checkbox-' + skill.name"
            name="skills" 
            v-model="skills" 
            :disabled="alreadyTrained(skill.name)" 
            :checked="alreadyTrained(skill.name)" >
          </td>
          <td class="prof-bonus">{{displayProfBonus(skill.name)}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import skillData from "../../data/skills.json";

export default {
  data: function() {
    return {
      skillData: skillData,
      skills: [],
      abilities: ["strength", "dexterity", "constitution", "intelligence", "wisdom", "charisma"],
    };
  },
  mounted() {
    this.skills = this.alreadyTrainedSkills;
  },
  watch: {
    selectedClass: function(klass) {
      this.$store.dispatch("characterCreator/setClass", klass);
    }
  },
  computed: {
    character() {
      return this.$store.state.characterCreator;
    },
    alreadyTrainedSkills() {
      let skills = []
      if (this.character.level1AncestryFeat.skills) {
        skills = skills.concat(this.character.level1AncestryFeat.skills)
      }
      if (this.character.backgroundSkill) {
        skills.push(this.character.backgroundSkill);
      }
      if (this.character.classSkills) {
        skills = skills.concat(this.character.classSkills);
      }
      return skills;
    },
    alreadyTrainedLores() {
      let lore = []
      if (this.character.level1AncestryFeat.lore) {
        lore = lore.concat(this.character.level1AncestryFeat.lore)
      }
      if (this.character.backgroundLore) {
        lore = lore.push(this.character.backgroundLore);
      }
      return lore;
    }
  },
  methods: {
    calcMod(ability) {
      return Math.floor((ability - 10) / 2);
    },
    displayMod(ability) {
      let mod = this.calcMod(ability);
      mod = (mod  > 0) ? '+' + mod : mod;
      return mod;
    },
    alreadyTrained(skillName){
      return this.alreadyTrainedSkills.includes(skillName);
    },
    profBonus(skillName){
      return this.skills.includes(skillName) ? 3 : 0;
    },
    displayProfBonus(skillName){
      return this.skills.includes(skillName) ? '+3' : '0';
    },
    skillBonus(skill) {
      return this.calcMod(this.character[skill.ability]) + this.profBonus(skill.name);
    },
    displaySkillBonus(skill) {
      return this.skillBonus(skill) > 0 ? "+" + this.skillBonus(skill) : this.skillBonus(skill);
    }
  }
};
</script>

